$header-letter-gray: #f8f9fa;
$header-background: #ced4da;
$background-footer: #343a40;

.main__container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main--section {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background-color: $header-background;
  color: $header-letter-gray;
  height: 130px;
}
.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15%;
  img {
    width: 100px;
    height: 100px;
  }
}
.nav--bar {
  display: flex;
  justify-content: space-around;
  width: 35%;
  a {
    text-decoration: none;
    color: $header-letter-gray;
  }
}
footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: $background-footer;
  width: 100%;
  height: 300px;
  .nav--bar--f {
    display: flex;
    justify-content: space-around;
    width: 35%;
    a {
      text-decoration: underline;
      color: blue;
      font-size: 1.2rem;
    }
  }
  .social {
    color: $header-letter-gray;
    a {
      color: blue;
      text-decoration: underline;
      font-size: 1.2rem;
    }
  }
}

.title {
  display: block;
  text-align: left;
  width: 80%;
  height: 100%;
}

.picture {
  display: block;
  width: 80%;
  img {
    width: 100%;
  }
}

.openinghours {
  display: flex;
  width: 80%;
  font-size: 16px;
}

.hours {
  width: 80%;
}

.table {
  width: 80%;
}

.adress {
  width: 80%;
}

.map {
  display: block;
  width: 80%;
  img {
    width: 100%;
  }
}
.main--section--third {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 80%;
}

.coffies,
.coffies-two {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 20px;
}

.coffie {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 45%;
  background-color: rgb(218, 218, 218);
  img {
    width: 100%;
  }

  h3 {
    padding-left: 5px;
  }
  p {
    padding-left: 5px;
  }
}

/*html2 -------------- Menu*/
h2 {
  display: block;
  margin-left: 38px;
}

.menu {
  display: flex;
  flex-direction: column;
  align-content: center;
  margin: 10px;
  width: 80%;
}

ul {
  list-style: none;
}

.nonalc {
  display: flex;
  flex-direction: row;
  flex-direction: column;
  align-content: space-between;
}
.no-alc {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.coffee__info {
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.empty {
  width: 100px;
  height: 5px;
  background-color: white;
  color: white;
}

.img_machine {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  img {
    width: 100%;
  }
}
.middle {
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-direction: flex-start;
  align-items: flex-start;
}

@media screen and (max-width: 1000px) {
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-color: $header-background;
    color: $header-letter-gray;
    height: 300px;
  }

  .nav--bar {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
  }

  footer {
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    .nav--bar--f {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  .social {
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h2 {
      font-size: 0.8rem;
    }
  }
  .coffie {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 20px;
    width: 100%;
  }
  .coffies-two {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 20px;
    width: 100%;
  }

  .main--section--third {
    width: 100%;
  }
  .main--section {
    flex-direction: column;
    align-items: center;
  }
  .coffies {
    width: 100%;
    flex-direction: column;
  }

  .coffee__info {
    flex-direction: column;
  }
  .no-alc {
    flex-direction: column;
  }

  .h2main {
    margin: 0;
  }
  .openinghours {
    flex-direction: column;
  }
}
